.card {
  width: 200px;
  border-radius: 5px;
  border: 1px solid #F3F3F3;
  background: $white-color;
  line-height: 14px;
  box-sizing: border-box;
  transition: all $effect-duration ease-in-out;

  &__content {
    padding: 0 16px 16px;
  }

  &__image {
    width: 160px;
    height: 134px;
    margin: 32px 19px 38px;
    background-size: cover;
  }

  &__title {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $main-font-color;
    margin-bottom: 4px;
  }

  &__subtitle {
    color: $secondary-color;
    font-size: 10px;
    font-weight: 400;
    margin-bottom: 16px;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-top: 0;

    &--last {
      margin: 24px 0 16px;
    }
  }

  &__reviews {
    text-align: right;
    font-size: 10px;
    font-weight: 400;
    margin-top: 2px;
    color: $main-font-color;
  }

  &__price-title {
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: $secondary-color;
  }

  &__price {
    text-align: right;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    width: 100%;
    height: 40px;
    text-align: center;
    color: $white-color;
    border-radius: 5px;
    background-color: $accent-color;
    border: none;

    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;

    &:hover {
      border: 1px solid $accent-color;
      background: $white-color;
      color: $accent-color;
    }
  }

  &:hover .card__button {
    border: 1px solid $accent-color;
    background: $white-color;
    color: $accent-color;
  }

  &:hover .card__title {
    color: $title-hover-color;
  }

  &:hover {
    transform: scale(1.2);
  }
}
