html, body, h1, h2, div, img, p, nav, header, li, a, ul {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.container {
  --colums: 1;
  grid-template-columns: repeat(var(--colums), minmax(200px, 1fr));
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --colums: 2;
  }
}

@media (width >= 768px) {
  .container {
    --colums: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --colums: 4;
    max-width: 944px;
    margin: 0 auto;
  }
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  transition: all .3s ease-in-out;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__item {
  text-align: center;
  text-transform: uppercase;
  color: #060b35;
  margin: 0 10px;
  list-style-type: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  cursor: pointer;
  color: #060b35;
  box-sizing: border-box;
  height: 60px;
  padding-top: 23px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.is-active, [data-qa="nav-hover"]:hover, .nav-link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: block;
  position: relative;
  top: 19px;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  line-height: 14px;
  transition: all .3s ease-in-out;
}

.card__content {
  padding: 0 16px 16px;
}

.card__image {
  background-size: cover;
  width: 160px;
  height: 134px;
  margin: 32px 19px 38px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__row {
  justify-content: space-between;
  margin-top: 0;
  display: flex;
}

.card__row--last {
  margin: 24px 0 16px;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover, .card:hover .card__button {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  justify-content: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  list-style: none;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

